import React from 'react'
import styles from '../../styles/styles.module.css'

import NavigationBar from '../../components/NavigationBar'
import Home from '../../components/Home'

function Portuguese() {
  const scroolDiv = (id) =>{
    document.getElementById(id)?.scrollIntoView({ behavior:'smooth', block: 'start' })
  }

  return (
    <div className={styles.container_home}>
      <NavigationBar 
        option1="Home"
        option2="Sobre mim"
        option3="Projetos"
        option4="Contato"
        option5="Agradecimento"
        
        languageTitle="Language"
        optionLang1="Brazilian Portuguese"
        optionLang2="English"

        onNavigate={scroolDiv}
      />

      <Home 
        titleHome="Milena's Portflio"
        paragraphHome="Ol! Seja bem vindo(a) me chamo Milena, uma profissional em constante evoluo na rea de desenvolvimento de sistemas. 
        Ao longo da minha trajetria, busco aprimorar minhas habilidades em linguagens de programao e frameworks utilizadas atualmente Neste portflio, 
        compartilho alguns dos projetos que refletem meu aprendizado e empenho em criar solues eficientes e inovadoras, sempre em busca de novos desafios 
        e oportunidades para crescer profissionalmente"
        buttonHome="Explorar"


        titleAboutMe="Sobre Mim"
        paragraphAboutMe="Profissional em Anlise e Desenvolvimento de Sistemas com experincia no desenvolvimento de aplicaes web fullstack, visualizao 
        e modelagem de bancos de dados. Entusiasta de tecnologia, resoluo de problemas e otimizao de processos, em busca de aprendizado contnuo."
        titleAboutMe2="Conhecimentos"
        p1="Linguagem de Programao"
        p2="Frameworks"
        p3="Linguagem de Estilizao"
        p4="Outras Ferramentas"
        p5="Linguagem de Marcao"
        p6="Idiomas"
        p7="Ingls (B1)"


        titleProjects="Projetos"


        titleContact="Contato"
        paragraphContact="Gostou ou tem alguma dvida? Fique  vontade para entrar em contato! Estou disponvel para novas oportunidades e colaboraes. 
        Voc pode me encontrar pelos seguintes canais"


        titleThanks="Agradecimento"
        paragraphThanks="Agradeo a visita! A tecnologia  uma fora poderosa que conecta pessoas e transforma ideias em realidade, podendo-se explorar 
        suas infinitas possibilidades e aplicar o conhecimento para criar solues que faam a diferena. Cada projeto compartilhado representa no apenas 
        aprendizado, mas tambm o impacto que a tecnologia pode ter no mundo. Fico grata pelo seu interesse e feedbacks sero bem vindos."

        onNavigate={scroolDiv}
      />
    </div>
  )
}

export default Portuguese